.wave {
  position: absolute;
  z-index: 1;
  &.reversed {
    transform: scale(-1, 1);
  }
  path {
    stroke: var(--yellow-light);
  }
}
@media only screen and (max-width: 600px) {
  .wave {
    transform: scale(0.7, 0.7) translate(21%, 300%);
    &.reversed {
      transform: scale(-0.7, 0.7) translate(21%, 300%);
    }
  }
}
